  border-radius: 2px;
}
.content-wrapper .content {
  display: inline-block;
  margin-left: 16px;
}
.content-wrapper .content .title {
  margin: 2px 0 8px 0;
  line-height: 16px;
}
.content-wrapper .content .title .brand {
  display: inline-block;
  width: 30px;
  height: 18px;
