
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
}
.cartcontrol .cart-decrease .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}
.cartcontrol .cart-count {
  display: inline-block;
  font-size: 10px;
  line-height: 24px;
  width: 12px;
  vertical-align: top;
  padding-top: 6px;
  color: #93999f;
}
.cartcontrol .cart-add {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}
.cartcontrol .fade-enter-active,
.cartcontrol .fade-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.cartcontrol .fade-enter-active .inner,
.cartcontrol .fade-leave-active .inner {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartcontrol .fade-enter,
.cartcontrol .fade-leave-to {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0);
          transform: translate3d(24px, 0, 0);
}
.cartcontrol .fade-enter .inner,
.cartcontrol .fade-leave-to .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}


